import webdav from 'webdav';
import axios from '@nextcloud/axios';
import parseUrl from 'url-parse';
import { generateRemoteUrl } from '@nextcloud/router'; // force our axios

var patcher = webdav.getPatcher();
patcher.patch('request', axios); // init webdav client on default dav endpoint

var remote = generateRemoteUrl('dav');
var client = webdav.createClient(remote);
export var remotePath = parseUrl(remote).pathname;
export default client;